#slideShow {

    display: block;
    min-height: 10rem;
    position: relative;
    overflow: hidden;

    #loader {
      position: absolute;
      width: 100%;
      top: 40%;
      display: block;
      text-align: center;
    }

    .loading {
      display: none;
    }


    #textSlide {
      position: absolute;
      text-align: center;
      padding-left: 17%;
      padding-top: 14%;
      color: #6f89b1;
      width: 40%;

      @include media(phone){
        width: 50%;
      }

      h1 {
        font-family: linotype_didotroman, "Times New Roman";
        font-weight: normal;
        font-size: calc(10px + 1vw);
        padding-top: 2vw;
        padding-bottom: 2vw;
      }

      .button {
        width: 25%;

        @include media(phone) {
          width: 50%;
        }
      }

    }


    #telSlide {
      font-family: "Helvetica";
      font-size: calc(10px + 1vw);
      font-weight: normal;
      position: absolute;
      bottom: 2%;
      right: 2%;
      color: #fff;

      p {
        font-weight: lighter;
        font-size: .7vw;
      }
    }

    #btSlideContainer {
      position: absolute;
      width: 100%;
      top: 50%;
      display: flex;
      justify-content: space-between;

      .btSlide {
        position: relative;
        display: block;
        color: #fff;

        width: 2.5vw;
        height: 2.5vw;
        cursor: pointer;
        transition: 0.6s ease;


        .arrow {
          font-size: 2vw;
          position: absolute;
          display: block;
          text-align: center;
          top: 0;
          padding-top: .2vw;
          width: 100%;
          height: 100%;
        }

        .bg {
          width: 100%;
          height: 100%;
          background-color: #fff;
          opacity: .3;
        }

        &#prev {
          margin-left: 2%;
        }

        &#next {
          margin-right: 2%;
        }

        &:hover {
          color: #000;
          background-color: #fff;
        }

      }
    }

    #imgSlide {
      width: 200%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

  }

